import {h, Component} from 'preact';
import Card from 'preact-material-components/Card';
import 'preact-material-components/Card/style.css';
import 'preact-material-components/Button/style.css';
import style from './style';
import {route} from 'preact-router';
import Icon from 'preact-material-components/Icon';
import {getImageSize} from '../../utils/responsive'

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.handleResize = this.handleResize.bind(this)
    // this.getImageSize = this.getImageSize.bind(this)
    if (typeof window !== "undefined") {
      this.state = {
        imageSize: getImageSize(window.innerWidth, 1280, window.innerHeight, 960),
        isMobile: isMobile()
      }
    } else {
      this.state = {
        imageSize: {
          width: '100%',
          height: '100%'
        },
        isMobile: false
      }
    }
  }

  componentWillMount() {
    this.handleResize();
  }
  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }
  handleResize() {
    if (typeof window !== "undefined") {
      this.setState({
        imageSize: getImageSize(document.body.clientWidth, 1280, window.innerHeight, 2000),
        isMobile: isMobile()
      })
    }
  }

  // <div class={`${style.home} page`}>
  // </div>
  render() {
    return (<div style={{

        // backgroundColor: 'rgb(176, 159, 115)',
        // backgroundImage: 'url(/assets/images/la-vida-la-bg-intro.JPG)',

        width: '100%',
        height: '150%',
        overflow: 'hidden'
      }}>

      <div name='crisalidas' style={{
          width: `${this.state.imageSize.width}px`,
          height: `${this.state.imageSize.height}px`,
          marginTop: `${this.state.isMobile
            ? '16vh'
            : '0vh'}`,
          backgroundImage: 'url(/assets/images/crisalidas.gif)',
          // backgroundSize: 'cover',
          backgroundSize: `${this.state.imageSize.width}px ${this.state.imageSize.height}px`,
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center top',
          margin: `2rem auto auto`
        }}>

        <div style={{
            width: `${this.state.imageSize.width}px`,
            // width: '100vw',
            height: `${this.state.imageSize.height}px`,
            // maxWidth: '90vh',
            // backgroundImage: 'url(/assets/images/crisalidas.png)',
            // backgroundSize: 'contain',
            // backgroundRepeat: 'no-repeat',
            // backgroundPosition: 'top center',
            // backgroundColor: '#b09f73',
            position: 'relative',
            fontFamily: 'Sign Painter',
            writingMode: 'vertical-rl',
            textOrientation: 'upright',
            fontSize: '1.5rem',
            textTransform: 'uppercase'
          }}>
          <div href="/canciones" onClick={() => route('/canciones')} title="Canciones" class={`${style.portadaLink}`} style={{
              left: '5%',
              top: `${this.state.isMobile
                ? '0%'
                : '16%'}`,
              width: '14.06%',
              height: `${this.state.isMobile
                ? '100%'
                : '122vh'}`
            }}></div>
          <div onClick={() => route('/obras-plasticas')} href="/obras-plasticas" title="Obras plsticas" class={`${style.portadaLink}`} style={{
              left: '24%',
              top: `${this.state.isMobile
                ? '0%'
                : '16%'}`,
              width: '14.06%',
              height: `${this.state.isMobile
                ? '100%'
                : '122vh'}`
            }}></div>
          <div onClick={() => route('/fotos-y-videos')} href="/fotos-y-videos" title="Fotos y vdeos" class={`${style.portadaLink}`} style={{
              left: '42%',
              top: `${this.state.isMobile
                ? '0%'
                : '16%'}`,
              width: '14.06%',
              height: `${this.state.isMobile
                ? '100%'
                : '122vh'}`
            }}></div>
          <div onClick={() => route('/material-tea')} href="/material-tea" title="Material TEA" class={`${style.portadaLink}`} style={{
              left: '60%',
              top: `${this.state.isMobile
                ? '0%'
                : '16%'}`,
              width: '14.06%',
              height: `${this.state.isMobile
                ? '100%'
                : '122vh'}`
            }}></div>
          <div onClick={() => route('/ficha-tecnica')} href="/ficha-tecnica" title="Ficha tcnica" class={`${style.portadaLink}`} style={{
              left: '80%',
              top: `${this.state.isMobile
                ? '0%'
                : '16%'}`,
              width: '14.06%',
              height: `${this.state.isMobile
                ? '100%'
                : '122vh'}`
            }}></div>
        </div>
      </div>
    </div>);
  }
}
function isMobile() {
  // debugger
  if (typeof window !== "undefined") {
    if (window.innerWidth < 630) {
      return true
    }
    return false
  }
}
